<template>
  <div class="footer">
    <div class="footer-wallet flex-center">
      <span v-if="address" class="address">{{address.substring(0,8) + '......' + address.substring(address.length-8, address.length)}}</span>
      <van-button v-else @click="initWallet" class="wallet-btn" type="primary">{{t('web3.連接錢包')}}</van-button>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs, computed, watch } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import web3Data from "@/utils/initWeb3";

export default {
  setup () {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      address: computed(() => store.state.accounts[0]),
      t: t,
      initWallet: () => {
        web3Data.initWeb3()
      },
      initData: () => {
      }
    })
    if (data.address) {
      data.initData();
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initData()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.footer-wallet {
  width: 100%;
  height: 55px;
  background: #ffffff;
  box-shadow: 0px -2px 10px 0px rgba(105, 164, 184, 0.75);
  padding: 6px 15px;
}
.wallet-btn {
  width: 100%;
  height: 44px;
  background: #409dbd;
  border-radius: 10px;
  border: initial;
}
</style>
