<template>
  <div class="head">
    <van-nav-bar :title="title" left-text="" :class="[{'active': headerActive},{'back-hader':Headtype === 'back'}]" left-arrow>
      <template #left v-if="Headtype === 'back'">
        <img class="back" src="@/assets/images/back@2x.png" @click="$goBack" alt="">
      </template>
    </van-nav-bar>
  </div>
</template>

<script>
import { reactive, toRefs, computed } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import { getLang, setLang } from "@/utils/i18n";
export default {
  props: {
    Headtype: {
      type: String,
      default: ""
    },
    title: {
      type: String,
      default: ""
    }
  },
  setup (props) {
    const store = useStore();
    const { t } = useI18n();
    const data = reactive({
      t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      Headtype: props.Headtype,
      title: props.title,
      showPopover: false,
      headerActive: false,
      local: getLang() || "zh-TW",
      actions: [
        {
          text: 'English',
          value: 'en'
        },
        {
          text: '中文繁體',
          value: 'zh-TW'
        }
      ],
      changeLanuage: (val) => {
        setLang(val.value)
        data.local = val.value
      }
    })
    const debounce = (fn, interval) => {
      var timer;
      var gapTime = interval || 150;
      return function () {
        clearTimeout(timer);
        var context = this;
        var args = arguments;
        timer = setTimeout(function () {
          fn.call(context, args);
        }, gapTime);
      };
    }
    document.addEventListener('scroll', debounce(() => {
      let scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      data.headerActive = scrollTop > 30
    }))
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
};
</script>
<style lang="less" scoped>
.head {
  color: #ffffff;
  font-size: 14px;
  font-family: PingFang SC;
  font-weight: 500;
  color: #ffffff;
  line-height: 35px;
}
.back {
  width: 10px !important;
  height: 18px;
}
.arrDown {
  width: 11px !important;
  height: 11px;
  margin-left: 8px;
}
.arrUp {
  transform: rotateX(180deg);
}
</style>
<style lang="less">
#app {
  .van-nav-bar {
    &.active {
      transition: all 0.3s;
      background: #75aec1;
      box-shadow: 0 1px 2px #598594;
    }
    &.back-hader {
      transition: all 0.3s;
      background: #75aec1;
      box-shadow: initial;
    }
  }
}
</style>
