<template>
  <div class="index-bg p15">
    <HeadTop title="管理界面"></HeadTop>
    <h1>禁止提币</h1>
    <div class="content">
      <div class="li">
        <van-cell-group inset title="添加名单">
          <van-field v-model="etx.whiteAddress" placeholder="钱包地址" type="text" />
        </van-cell-group>
        <van-button class="confim-btn" type="primary"  @click="addWhitelist">{{t('index.確定')}}</van-button>
      </div>
       <div class="li">
        <van-cell-group inset title="取消名单">
          <van-field v-model="etx.delWhiteAddress" placeholder="钱包地址" type="text" />
        </van-cell-group>
        <van-button class="confim-btn" type="primary"  @click="delWhitelist">{{t('index.確定')}}</van-button>
      </div>
    </div>
    <h1>已添加地址</h1>
     <div class="content">
      <div class="li" v-for="item in list">
      {{item}}
    </div>
    </div>
    <Footer></Footer>
  </div>
</template>
<script>
import HeadTop from '@/components/HeadTop'
import { reactive, toRefs, computed, watch, onMounted } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import Footer from "@/components/Footer";
import { config } from "@/utils/config";
import tokenAbi from "@/utils/abi";

export default {
  components: {
    HeadTop,
    Footer
  },
  setup () {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      lpStakeContract: null,
      nodePoolContract: null,
      etxContract: null,
      mainPoolContract: null,
      etx: {
        whiteAddress: '',
        delWhiteAddress: ''
      },
      list: [],
      initContract: () => {
        data.etxContract = new data.web3.eth.Contract(
          tokenAbi,
          config.etxAddress
        );
        data.initData()
      },
      initData: () => {
        data.getWhitelist()
      },
      getWhitelist: async () => {
        let length = await data.etxContract.methods.getWhitelistLength().call({from: data.address})
        let arr = []
        for(let i = 0; i < length; i++) {
          let address = await data.etxContract.methods.getWhitelist(i).call({from: data.address})
          arr.push(address)
        }
        data.list = arr
      },
      addWhitelist: async () => {
        await data.etxContract.methods.addWhitelist(data.etx.whiteAddress).send({from: data.address})
        data.initData()
      },
       delWhitelist: async () => {
        await data.etxContract.methods.delWhitelist(data.etx.delWhiteAddress).send({from: data.address})
        data.initData()
      }
      
    })
    onMounted(() => {
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less">
.van-cell-group {
  margin: 0 0 0;
}
.confim-btn {
  width: 125px;
  height: 40px;
  background: #409dbd;
  border-radius: 10px;
  font-size: 15px;
  font-weight: bold;
  color: #ffffff;
  margin: 12px 0 20px;
  border: 0;
}
.van-cell-group__title{
  font-size: 16px;
  color: #f00;
  padding: 10px 0;
}
</style>
<style lang="less" scoped>
.index-bg {
  background: #75aec1 url('~@/assets/images/index-bg.png') no-repeat;
  background-size: 100% auto;
  width: 100%;
  min-height: 100vh;
  line-height: 1.3;
}
h1{
  color: #fff;
  margin-bottom: 15px;
}
</style>
